<script>
export default {
  name: "BoardDelete",
  created() {
    this.$store.dispatch("deleteBoard", this.$route.params.no).then((resp) => {
      let msg = "  .";
      if (resp.data === "success") {
        msg = "  .";
      }
      alert(msg);
      this.$router.push("/board/list");
    });
  },
};
</script>

