<template lang="">
  <!--   -->
  <div>
    <form>
       : <base-input type="text" v-model="board.btitle" /><br />
       :
      <textarea v-model="board.bcontent" class="form-control" rows="3"></textarea><br />
      <base-button @click.prevent="boardSubmit" type="primary" size="sm" class="float mt-3 mb-3"
        ></base-button
      >
    </form>
    <router-link to="/board"></router-link>
  </div>
</template>
<script>
import { mapGetters } from "vuex";
export default {
  computed: {
    ...mapGetters(["board"]),
  },
  created() {
    let bnum = this.$route.params.no;
    this.$store.dispatch("getBoard", { bnum });
  },
  methods: {
    boardSubmit() {
      this.$store.dispatch("updateBoard", this.board).then((resp) => {
        let msg = "  .";
        if (resp.data === "success") {
          msg = "  .";
        }
        alert(msg);
      });
      this.$router.push("/board/detail/" + this.board.bnum);
    },
  },
};
</script>
<style lang=""></style>
