<template>
  <!--  Vue -->
  <div>
    <div id="map" style="width: 100%; height: 500px"></div>
    <div hidden>
      {{ aptlist }}
      {{ aptName }}
    </div>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
export default {
  name: "KakaoMap",
  data() {
    return {
      container: Document,
    };
  },
  props: {
    islist: Boolean,
    aptlist: Array,
    si: String,
    gugun: String,
    dong: String,
    aptName: String,
    jibun: String,
  },
  computed: {
    ...mapGetters(["subwaylist"]),
  },
  created() {
    this.$store.dispatch("getSubwayList");
  },
  mounted() {
    window.kakao && window.kakao.maps ? this.initMap() : this.addScript();
  },
  updated() {
    this.addMarker();
  },
  methods: {
    // 
    initMap() {
      var container = document.getElementById("map");
      var options = {
        center: new kakao.maps.LatLng(33.450701, 126.570667),
        level: 3,
      };
      var map = new kakao.maps.Map(container, options);
      var marker = new kakao.maps.Marker({
        position: map.getCenter(),
      });
      marker.setMap(map);
    },
    //script   
    addScript() {
      const script = document.createElement("script");
      // /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "http://dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=3a2dc22797715739695f9d702a74201a&libraries=services,clusterer";
      document.head.appendChild(script);
    },
    //   
    addMarker() {
      // islist = false ->  
      if (!this.islist) {
        //  
        var mapOption = {
          center: new kakao.maps.LatLng(33.450701, 126.570667),
          level: 4,
        };
        this.container = document.getElementById("map");

        var map = new kakao.maps.Map(this.container, mapOption); // -   
        var geocoder = new kakao.maps.services.Geocoder(); //   
        var imageSrc = "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png";

        var imageSize = new kakao.maps.Size(24, 35);
        var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize); //   
        var addrFullName = this.dong + " " + this.jibun + ", " + this.aptName;

        var sList = this.subwaylist;

        geocoder.addressSearch(addrFullName, function (result, status) {
          if (status === kakao.maps.services.Status.OK) {
            var coords = new kakao.maps.LatLng(result[0].y, result[0].x);
            var marker = new kakao.maps.Marker({
              map: map,
              position: coords,
              image: markerImage,
            });

            sList.forEach((element) => {
              geocoder.addressSearch(element.address, function (s_result, s_status) {
                if (s_status === kakao.maps.services.Status.OK) {
                  var s_coords = new kakao.maps.LatLng(s_result[0].y, s_result[0].x); // 

                  var polyline = new kakao.maps.Polyline({
                    /* map:map, */
                    path: [s_coords, coords],
                    strokeWeight: 2,
                    strokeColor: "#FF00FF",
                    strokeOpacity: 0.8,
                    strokeStyle: "dashed",
                  });

                  var distance = Math.floor(polyline.getLength()); //    

                  if (distance < 5000) {
                    var marker = new kakao.maps.Marker({
                      map: map,
                      position: s_coords,
                      title: element.name + "",
                    });

                    //    (  :  4km/h ,   : 67m/min)
                    var walkkTime = (distance / 67) | 0;
                    var walkHour = "",
                      walkMin = "";

                    if (walkkTime > 60) {
                      walkHour =
                        '<span class="number">' + Math.floor(walkkTime / 60) + "</span> ";
                    }
                    walkMin = '<span class="number">' + (walkkTime % 60) + "</span>";

                    //    (   : 16km/h,     : 267m/min)
                    var bycicleTime = (distance / 227) | 0;
                    var bycicleHour = "",
                      bycicleMin = "";

                    if (bycicleTime > 60) {
                      bycicleHour =
                        '<span class="number">' + Math.floor(bycicleTime / 60) + "</span> ";
                    }
                    bycicleMin = '<span class="number">' + (bycicleTime % 60) + "</span>";

                    //HTML Content
                    var content = '<div class="">';
                    content += `        <span class="label">${element.name} ${element.line} </span><br>`;
                    content +=
                      '        <span class="label"> : </span><span class="number">' +
                      distance +
                      "m</span><br>";
                    content +=
                      '        <span class="label"> : </span>' + walkHour + walkMin + "<br>";
                    content +=
                      '        <span class="label"> : </span>' + bycicleHour + bycicleMin;
                    content += "</div>";
                    var infowindow = new kakao.maps.InfoWindow({
                      content: '<div style="width:200px;">' + content + "</div>",
                      removable: true,
                    });

                    marker.setMap(map);

                    kakao.maps.event.addListener(marker, "click", function () {
                      infowindow.open(map, marker);
                    });
                  }
                }
              });
            });

            var infowindow = new kakao.maps.InfoWindow({
              content:
                '<div style="width:150px;text-align:center;padding:6px 0;">' +
                addrFullName +
                " " +
                "</div>",
            });
            infowindow.open(map, marker);
            marker.setMap(map);
            map.setCenter(coords);
          }
        });
      }
      //         
      else if (this.aptlist.length == 0) {
        mapOption = {
          center: new kakao.maps.LatLng(33.450701, 126.570667),
          level: 4,
        };
        this.container = document.getElementById("map");
        map = new kakao.maps.Map(this.container, mapOption);
        geocoder = new kakao.maps.services.Geocoder();
        imageSrc = "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png";

        addrFullName = this.si + " " + this.gugun + " ";
        geocoder.addressSearch(addrFullName, function (result, status) {
          if (status === kakao.maps.services.Status.OK) {
            var coords = new kakao.maps.LatLng(result[0].y, result[0].x);
            var marker = new kakao.maps.Marker({
              map: map,
              position: coords,
            });
            var infowindow = new kakao.maps.InfoWindow({
              content:
                '<div style="width:150px;text-align:center;padding:6px 0;">' +
                "    ." +
                "</div>",
            });
            infowindow.open(map, marker);
            marker.setMap(map);
            map.setCenter(coords);
          }
        });
      } else {
        mapOption = {
          center: new kakao.maps.LatLng(33.450701, 126.570667),
          level: 5,
        };
        this.container = document.getElementById("map");
        map = new kakao.maps.Map(this.container, mapOption);
        geocoder = new kakao.maps.services.Geocoder();
        imageSrc = "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/markerStar.png";

        this.aptlist.forEach((element) => {
          var imageSize = new kakao.maps.Size(24, 35);
          var markerImage = new kakao.maps.MarkerImage(imageSrc, imageSize);
          var addrFullName = element.dong + " " + element.jibun + ", " + element.aptName;
          geocoder.addressSearch(addrFullName, function (result, status) {
            if (status === kakao.maps.services.Status.OK) {
              var coords = new kakao.maps.LatLng(result[0].y, result[0].x);
              var marker = new kakao.maps.Marker({
                map: map,
                position: coords,
                image: markerImage,
                title: element.,
              });
              var infowindow = new kakao.maps.InfoWindow({
                content:
                  '<div style="width:200px;text-align:center;padding:20px 0;">' +
                  addrFullName +
                  " " +
                  "</div>",
                removable: true,
              });
              marker.setMap(map);

              kakao.maps.event.addListener(marker, "click", function () {
                infowindow.open(map, marker);
              });

              map.setCenter(coords);
            }
          });
        });
      }
    },
  },
};
</script>
<style>
.number {
  font-weight: bold;
  color: #ee6152;
}
.distanceInfo {
  position: relative;
  top: 5px;
  left: 5px;
  list-style: none;
  margin: 0;
}
.distanceInfo .label {
  display: inline-block;
  width: 50px;
}
.distanceInfo:after {
  content: none;
}
</style>
