<template>
  <!--  /  Form -->
  <div>
    <form class="form-inline">
      <div class="form group">
        <select v-model="search.key" class="form-control">
          <option disabled value=""></option>
          <option value="aptName"> </option>
          <option value="dong"></option>
        </select>
      </div>
      <base-input class="form-group" placeholder=" " v-model="search.word">
      </base-input>

      <base-button
        type="secondary"
        class="form-control my-4 form-group"
        @click.prevent="searchSubmit"
        ></base-button
      >
    </form>
  </div>
</template>

<script>
export default {
  name: "search",
  data() {
    return {
      search: {
        key: "",
        word: "",
      },
    };
  },

  methods: {
    searchSubmit() {
      this.$router.push(`/rent/search/${this.search.key}/${this.search.word}`);
    },
  },
};
</script>

